import { voidFun } from '@zyf2e/monitor-shared'

/**
 * try
 * ../param fn try
 * ../param errorFn err
 */
export function nativeTryCatch(fn: voidFun, errorFn?: (err: any) => void): void {
  try {
    fn()
  } catch (err) {
    console.log('err', err)
    if (errorFn) {
      errorFn(err)
    }
  }
}
